.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container h1 {
    margin-top: 1rem;
    font-size: 1.2rem;
}

.tableWrapper {
    align-items: center;
    padding: 0.8rem;
    margin: 2rem 0;
    width: 100%;
    overflow-x: auto;
}

.tableWrapper > table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    min-width: 600px;
    text-align: center;
    box-shadow: var(--shadow);
}

.tableWrapper td,
.tableWrapper th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 14px;
    text-align: center;
}

.tableWrapper tr:nth-child(even) {
    background-color: #fff;
}

.btn {
    width: 100%;
    display: inline-block;
    cursor: pointer;
    padding: .9rem 1.4rem;
    background: var(--primaryColor);
    color: var(--mainWhite);
    outline: none;
    border: none;
    border-radius: 10px;
}

.tableWrapper .btn:disabled {
    background-color: #a6a6a6;
    cursor: not-allowed;
  }

.btn:hover,
.btn:focus {
    background: var(--primaryColor);
    opacity: 0.8;
}


@media (max-width: 768px) {
    .container {
        width: 100%;
    }

    .btn {
        padding: 0.4rem 0.6rem;
        font-size: 0.8rem;
    }
    .tableWrapper{
        width: 100%;
    }
}

@media (max-width: 500px) {
    .btn {
        padding: 0.4rem 0.6rem;
        font-size: 0.8rem;
    }
}