.defaultMessage,
.dangerMessage {
    width: 100%;
    height: 50px;
    padding: 1.2rem 0;
    border: 2px solid #0fc;
    background-color: #0fc;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dangerMessage {
    border: 2px solid #f99;
    background-color: #f99;
}